
import yheader from '../components/ycheader'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'fundTotalAssets',
data() {
return {
fundInfo:{
fundAmount:0,
fundAgainst:0,
buildDate:'--',
unSubmitAmount:0,
marketValueAmount:0
},
fundList:[]
}
},
computed: {

},
components: {
yheader,
Flexbox,
FlexboxItem
},
mounted() {
this.getfundInfo();
this.getfundList();
},
methods: {
colorType(e){
if(e>=0){
return "_ff5255"
}else{
return "_36cca4"
}
},
getfundInfo(){
let _this = this;
this.post({
url: "/fundUser/fundInfo/v1.0",
success: function(e) {
if (e.code == "0000") {
_this.fundInfo=e.result.fundInfo;
}
}
})
},
getfundList(){
let _this = this;
this.post({
url: "/fundUser/fundDetail/v1.0",
success: function(e) {
if (e.code == "0000") {
_this.fundList=e.result.list;
}
}
})
}
}
}



